import React from 'react'
import { TotalGraph } from '../TotalGraph'
import { readString } from 'react-papaparse'
import { getData } from '../data/infections.js'
import './style.css'

export const Content = () => {
    let parsed = getData()
    var results = readString(parsed)

    let data = results['data']

    function getDates() {
        let dates = []
        data.map(x => dates.push(x[0]))
        return dates.splice(1)
    }

    function getTotalInfections() {
        var infections = []
        var i;
        for (i = 1; i < data.length; i++) {
            let curr = data[i]
            curr = curr.slice(1)
            let currSum = curr.reduce((a,b) => parseInt(a) + parseInt(b), 0)
            infections.push(currSum)
        }
        return infections
    }

    function getTotalCumulativeInfections() {
        let total = getTotalInfections()
        const cumulativeSum = (sum => value => sum += value)(0);
        return total.map(cumulativeSum)
    }

    let dates = getDates()
    let totalInfections = getTotalInfections()
    let cumulativeInfections = getTotalCumulativeInfections()

    return (
        <div className="content">
            <h2>Antall smittede: {cumulativeInfections[cumulativeInfections.length-1]}</h2>
            <br/>
            <TotalGraph x={dates} y={cumulativeInfections} />
        </div>
    )
}